import { useState } from 'react'
import { useDispatch } from 'react-redux'
import { fetchWrapper } from 'src/helpers/fetch-wrapper'
import { setTxId, setSubmitted } from '@store/optionSlice'
import useAllowance from '../hooks/useAllowance'
import { ModeOptions } from '@interface'
import { getTransactionId } from '@utils/functions'

const useSubmitTransaction = ({
  mode,
  amount,
  totalFee,
  // feeDeduct,
  originAddress,
  targetAddress,
  originChain,
  targetChain,
  originSymbol,
  targetSymbol,
  backendUrl,
  decimals
}: {
  mode: ModeOptions
  amount: bigint
  totalFee: bigint
  // feeDeduct: boolean
  originAddress: string
  targetAddress: string
  originChain: string
  targetChain: string
  originSymbol: string
  targetSymbol: string
  backendUrl: string
  decimals: number
}) => {
  const dispatch = useDispatch()

  const [isSubmitting, setSubmitting] = useState(false)

  const submitTransaction = async () => {
    try {
      setSubmitting(true)
      // const finalAmount =
      //   mode === ModeOptions.payment
      //     ? (+amount).toFixed(decimals || 6)
      //     : feeDeduct
      //       ? (+amount - totalFeeUsd).toFixed(decimals || 6)
      //       : (+amount).toFixed(decimals || 6)

      const params = JSON.stringify({
        originAddress,
        originChain,
        targetAddress,
        targetChain,
        originSymbol,
        targetSymbol,
        amount: amount.toString(),
        // amount: finalAmount,
        fee: totalFee.toString(),
        decimals,
        htlcCreationHash: '',
        htlcCreationVout: 0,
        htlcExpirationTimestamp: '0',
        htlcVersion: '',
        senderPubKey: ''
      })
      console.log('params: ', {
        decimals,
        totalFee,
        params: JSON.parse(params)
      })

      const transactionResult: any = await fetchWrapper.post(
        `${backendUrl}/submit`,
        params
      )

      if (transactionResult?.code !== 0) {
        setSubmitting(false)
        return { success: false, message: 'Failed to submit transaction' }
      }

      const transactionId = getTransactionId(transactionResult.events)

      dispatch(setTxId(transactionId))
      dispatch(setSubmitted(true))
      setSubmitting(false)

      return { success: true, message: 'Transaction submitted successfully.' }
    } catch (error) {
      console.error('Error submitting transaction:', error)
      setSubmitting(false)
      return { success: false, message: 'Failed to submit transaction' }
    }
  }

  return { submitTransaction, isSubmitting }
}

export default useSubmitTransaction
